  /* ------------------------------------------------ */

  const $chatContainer = $('.chat-messages');
  // speaker ËΩâÊèõÊàê type
  function mapSpeakerToType(speaker) {
    if (speaker === 'Turi') return 'teacher';
    if (speaker === 'UnvoicedNote') return 'unvoiced';
    return 'unknown';
  }

  // ÊâìÂ≠óÊïàÊûúÔºöÈÄêÂ≠óÈ°ØÁ§∫ÊñáÂ≠ó
  function typeText($element, text, speed = 50) {
    return new Promise(resolve => {
      let i = 0;
      const interval = setInterval(() => {
        $element.text($element.text() + text.charAt(i));
        i++;
        if (i >= text.length) {
          clearInterval(interval);
          resolve();
        }
      }, speed);
    });
  }

  // Êí≠ÊîæÈü≥Ë®ä
  function playAudio(src) {
    return new Promise(resolve => {
      const audio = new Audio(`audio/${src}`);
      console.log(`Playing audio: audio/${src}`);
      audio.onended = resolve;
      audio.onerror = resolve; // Ëã•Èü≥Ë®äÂ§±Êïó‰πüÁπºÁ∫åÊµÅÁ®ã
      audio.play();
    });
  }

  // È°ØÁ§∫‰∏ÄÂâáË®äÊÅØ
  async function showMessage(msg) {
    console.log(msg)
    const type = mapSpeakerToType(msg.speaker);
    const $template = $(`.message-block.message-${type}`).first();
    const $newMessage = $template.clone().hide();
    const $textElement = $newMessage.find('p');

    $textElement.text('');
    $('.chat-messages').append($newMessage);

    $chatContainer.scrollTop($chatContainer[0].scrollHeight);

    let typingPromise;
    let audioPromise = Promise.resolve();

    if (type === 'unvoiced') {
      $textElement.text(msg.text);
      typingPromise = new Promise(resolve => {
        $newMessage.fadeIn(300, resolve);
      });
    } else {
      $newMessage.show();
      $newMessage.find('.audio-icon').addClass('show');
      typingPromise = typeText($textElement, msg.text);

      if (msg.audio && type === 'teacher') {
        audioPromise = playAudio(msg.audio);
      }
    }

    await Promise.all([typingPromise, audioPromise]);

    $chatContainer.scrollTop($chatContainer[0].scrollHeight);

    if (type === 'unvoiced') {
      await new Promise(resolve => setTimeout(resolve, 500));
    } else {
      $newMessage.find('.audio-icon').addClass('pause');
      await new Promise(resolve => setTimeout(resolve, 500));
      $newMessage.find('.audio-icon').removeClass('show pause');
    }

    $chatContainer.scrollTop($chatContainer[0].scrollHeight);

    await new Promise(resolve => setTimeout(resolve, 1000));
  }

  // È°ØÁ§∫‰∏ÄÁµÑË®äÊÅØÔºàgreeting„ÄÅintro„ÄÅfeedbackÔºâ
  async function runLessonMessages(messages) {
    console.log("üëâ runLessonMessages Êî∂Âà∞ÁöÑ messages:", messages);
    for (const msg of messages) {
      await showMessage(msg);
    }
  }

  // Ë™≤Á®ãÊµÅÁ®ã
  async function runLesson(data) {
    // 1. greeting
    //---------------------
    await runLessonMessages(data.greeting);

    // 2. concepts
    for (let c = 0; c < data.concepts.length; c++) {
      const concept = data.concepts[c];

      // 3. ÂúñÁâáÔºàÂ¶ÇÊûúÊúâÔºâ
      if (concept.image) {
        const $img = $('<img>', {
          class: 'concept-image',
          src: `images/${concept.image}`,
          alt: 'concept image'
        });
        $('.notes-section .card-content').append($img).show(500);
      }

      // 4. intro
      await runLessonMessages(concept.intro);

      // 5. ÈÅ∏È†ÖÊåâÈàï
      for (let i = 0; i < concept.options.length; i++) {
        const option = concept.options[i];
        const $btn = $('<button>', {
          class: 'action-btn choice-btn',
          html: `${option.label}: ${option.text}`
        }).attr('data-concepts', c).attr('data-options', i);

        if (option.isCorrect) {
          $btn.data('correct', true);
        }

        $('#choicePopup').append($btn).addClass('show');
        $chatContainer.scrollTop($chatContainer[0].scrollHeight);
      }


      $('.choice-btn').on('click', async function () {
        const c = $(this).data('concepts'); 
        const o = $(this).data('options');
        const option = lessonData.concepts[c].options[o];
        // 6.1 Á¶ÅÁî®ÊâÄÊúâÊåâÈàï
        $('#choicePopup').removeClass('show');
        $chatContainer.scrollTop($chatContainer[0].scrollHeight);

        // 6.2 Êí≠ÊîæÈü≥Êïà
        await playAudio('SFX_Select_01.mp3');

        // 6.3 È°ØÁ§∫Â≠∏ÁîüË®äÊÅØ
        const $studentMsg = $(`.message-block.message-student`).first().clone();
        $studentMsg.find('p').text(`${option.label}: ${option.text}`);
        $('.chat-messages').append($studentMsg);
        //$('.chat-messages').scrollTop($('.chat-messages')[0].scrollHeight);
        $chatContainer.scrollTop($chatContainer[0].scrollHeight);

        // 6.4 feedback
        const turiFeedbacks = option.feedback.turi;
        const noteFeedbacks = option.feedback.note;

        console.log(option);

        // Èö®Ê©ü‰∏ÄÂÄã Turi
        let messagesToShow = [];
        if (Array.isArray(turiFeedbacks) && turiFeedbacks.length > 0) {
          const randomTuri = turiFeedbacks[Math.floor(Math.random() * turiFeedbacks.length)];
          messagesToShow.push(randomTuri);
          console.log(randomTuri);
        }

        // Êé•‰∏äÂÖ®ÈÉ®ÁöÑ note
        if (Array.isArray(noteFeedbacks) && noteFeedbacks.length > 0) {
          messagesToShow = messagesToShow.concat(noteFeedbacks);
        }

        console.log(messagesToShow);

        // ‰∏üÈÄ≤ runLessonMessages ‚Üí ÂÆÉÂ∞±Âè™Ë≤†Ë≤¨„Äå‰æùÂ∫èÊí≠Êîæ„Äç
        await runLessonMessages(messagesToShow);

        //6.5 Á≠îÈåØ ‚Üí reteach ‚Üí redo ‚Üí ÂïüÁî®ÊåâÈàï
        if (!option.isCorrect) {
          await runLessonMessages(concept.reteach);
          await runLessonMessages(concept.redo);
          $('#choicePopup').addClass('show');
          $chatContainer.scrollTop($chatContainer[0].scrollHeight);
        }
      });
    }
  }

